rgin-left: 1rem;\n    }\n\n    > .container {\n        flex: 1;\n    }\n\n    &.bg-primary{\n        @include navbar-colors($bg-primary, $white-color);\n    }\n    &.bg-info{\n        @include navbar-colors($bg-info, $white-color);\n    }\n\n    &.bg-warning{\n        @include navbar-colors($bg-warning, $white-color);\n    }\n\n    &.bg-rose{\n        @include navbar-colors($bg-rose, $white-color);\n    }\n\n    &.bg-danger{\n        @include navbar-colors($bg-danger, $white-color);\n    }\n\n    &.bg-success{\n        @include navbar-colors($bg-success, $white-color);\n    }\n    &.bg-dark{\n        @include navbar-colors($grey-900, $white-color);\n    }\n\n    &.navbar-transparent{\n        background-color: transparent !important;\n        box-shadow: none;\n        padding-top: 25px;\n\n    }\n\n  .notification{\n    position: absolute;\n    top: 5px;\n    border: 1px solid #FFF;\n    right: 10px;\n    font-size: 9px;\n    background: #f44336;\n    color: #FFFFFF;\n    min-width: 20px;\n    padding: 0px 5px;\n    height: 20px;\n    border-radius: 10px;\n    text-align: center;\n    line-height: 19px;\n    vertical-align: middle;\n    display: block;\n  }\n}\n\n.navbar{\n  .navbar-minimize{\n    padding: 3px 0 0 15px;\n  }\n\n  &.navbar-transparent{\n    padding-top: 10px;\n  }\n\n  .collapse{\n    .navbar-nav{\n      .nav-item .nav-link{\n        position: relative;\n        padding: 10px 15px;\n        font-weight: $font-weight-default;\n        font-size: $mdb-btn-font-size-base;\n        text-transform: uppercase;\n        border-radius: $border-radius-base;\n        line-height: 20px;\n        margin-left: 5px;\n        color: inherit;\n\n          &:not(.btn-just-icon) .fa{\n              position: relative;\n              top: 2px;\n              margin-top: -4px;\n              margin-right: 4px;\n          }\n\n          & .material-icons,\n          & .fa{\n              font-size: $font-size-lg;\n              max-width: 24px;\n              margin-top: -1.1em;\n          }\n\n          &:not(.btn) .material-icons{\n              margin-top: -3px;\n              top: 0px;\n              position: relative;\n              margin-right: 3px;\n          }\n          .notification{\n            top: 0px;\n          }\n        }\n    }\n  }\n}\n\n.off-canvas-sidebar{\n  .navbar{\n    .navbar-collapse{\n      .navbar-nav .nav-item{\n        .nav-link{\n          padding-top: 15px;\n          padding-bottom: 15px;\n          font-weight: 500;\n          font-size: 12px;\n          text-transform: uppercase;\n          border-radius: 3px;\n          color: $white-color;\n          margin: 0 15px;\n\n          &:hover{\n            background: rgba(200, 200, 200, 0.2);\n          }\n        }\n      }\n    }\n  }\n}\n","@mixin navbar-colors($color, $link-color) {\n    color: $link-color;\n    background-color: $color !important;\n    @include shadow-navbar-color($color);\n\n    .dropdown-item:hover,\n    .dropdown-item:focus{\n      @include shadow-small-color($color);\n      background-color: $color;\n      color: $link-color;\n    }\n\n    .navbar-toggler{\n        .navbar-toggler-icon{\n            background-color: $link-color;\n        }\n    }\n}\n",".alert {\n    border: 0;\n    border-radius: 3px;\n    position: relative;\n    padding: 20px 15px;\n    line-height: 20px;\n\n    b{\n        font-weight: $font-weight-bold;\n        text-transform: uppercase;\n        font-size: $font-size-small;\n    }\n    // SASS conversion note: please mirror any content change in _mixins-shared.scss alert-variations-content\n    @include alert-variations(unquote(\".alert\"), unquote(\"\"), $mdb-text-color-light);\n\n    &-info, &-danger, &-warning, &-success, &-rose {\n        color: $mdb-text-color-light;\n    }\n\n    &-default {\n        a, .alert-link {\n            color: $mdb-text-color-primary;\n        }\n    }\n\n    span{\n        display: block;\n        max-width: 89%;\n    }\n\n    &.alert-danger{\n        @include s