[disabled] + .toggle:active:after {\n        box-shadow: 0 1px 3px 1px rgba(0, 0, 0, 0.4), 0 0 0 15px rgba(0, 0, 0, 0.1);\n      }\n\n      // Ripple off and disabled\n      &:checked + .toggle:after {\n        left: 15px;\n      }\n    }\n\n    // set bg when checked\n    input[type=checkbox]:checked {\n      + .toggle {\n        background-color: rgba($brand-primary, (70/100)); // Switch bg on\n      }\n\n      + .toggle:after {\n        border-color: $brand-primary; // Handle on\n      }\n\n      + .toggle:active:after {\n        box-shadow: 0 1px 3px 1px rgba(0, 0, 0, 0.4), 0 0 0 15px rgba($brand-primary, (10/100)); // Ripple on\n      }\n    }\n  }\n}\n","// marker class (used as a selector for one-off elements to decorate)\n.ripple {\n  position: relative;\n}\n\n.ripple-container {\n  position: absolute;\n  top: 0;\n  left: 0;\n  z-index: 1;\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n  pointer-events: none;\n  border-radius: inherit;\n\n  .ripple-decorator {\n    position: absolute;\n    width: 20px;\n    height: 20px;\n    margin-top: -10px;\n    margin-left: -10px;\n    pointer-events: none;\n    background-color: rgba($black, 0.05);\n    border-radius: 100%;\n    opacity: 0;\n    transform: scale(1);\n    transform-origin: 50%;\n\n    &.ripple-on {\n      opacity: 0.1;\n      transition: opacity 0.15s ease-in 0s,\n        transform 0.5s cubic-bezier(0.4, 0, 0.2, 1) 0.1s;\n    }\n\n    &.ripple-out {\n      opacity: 0;\n      transition: opacity 0.1s linear 0s !important;\n    }\n  }\n}\n",".footer{\n    padding: $padding-general-x 0;\n    text-align: center;\n    display: -webkit-flex; /* Safari */ /* Safari 6.1+ */\n    display: flex;\n\n    ul{\n        margin-bottom: 0;\n        padding: 0;\n        list-style: none;\n\n        li{\n            display: inline-block;\n\n            a{\n                color: inherit;\n                padding: $padding-general-x;\n                font-weight: $font-weight-bold;\n                font-size: $mdb